._SeoLinkMain1 {
  height: auto;
  width: 100%;
  background: url("/public/Images/seopages/seo.png");
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-position: bottom;
  background-origin: border-box;
  position: relative;
}

.linkgifDenote {
  width: 340px;
  height: 400px;
  position: absolute;
  top: 39%;
  right: 29%;
}
.linkgifDenote > img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
._SeoLinkupperdiv {
  display: flex;
  /* border: 1px solid green; */
  width: 100%;
  height: 740px;
}
._SeoLinktext {
  /* border:1px solid white; */
  width: 40%;
  color: black;
  box-sizing: border-box;
  margin: auto;
  margin-left: 80px;
  /* text-align: center; */
}
._SeoLinkparagraph {
  /* border:1px solid white; */
  color: gray;
  font-weight: 400;
  width: 100%;
  line-height: 1.5;
  font-size: 16px;
}
._SeoLinkheading {
  /* border:1px solid white; */
  margin-bottom: 10px;
  width: 100%;
  font-family: sans-serif;
  font-size: 32px;
  font-weight: 600;
  /* text-align: left; */
}
/* ._SeoLinkheading ~ div{
font-size: 25px;
} */
._SeoLinkform {
  /* border:1px solid red; */
  width: 42%;
  height: 100%;
}
._SeoLinkformcontent {
  border: 1px solid white;
  width: 500px;
  height: 550px;
  background-color: white;
  border-radius: 35px;
  box-shadow: 3px 3px 40px rgb(0 0 0 / 15%);
  margin: auto;
  margin-top: 150px;
  box-sizing: border-box;
}
._SeoLinkformheading {
  /* border: 1px solid red; */
  /* border-radius: 35px; */
  padding-top: 20px;
  font-size: 25px;
  text-align: center;
  font-weight: 600;
  color: #605d5d;
}

._SeoLinkRatings {
  border: 1px solid green;
}
._SeoLinkenterform {
  /* border:1px solid red; */
  width: 80%;
  margin: auto;
  margin-top: 5px;
}
._seoLinkforminput {
  border: 1px solid #ef4c23;
  font-size: 18px;
  border-radius: 12px;
  margin-top: 17px;
  /* outline: #EF4C23; */
  padding: 7px;
  width: 100%;
  color: gray;
  font-weight: 400;
}
._seoLinkforminput ~ label {
  color: #ef4c23;
  font-size: 18px;
  font-weight: 400;
  /* border:1px solid rebeccapurple; */
  display: inline-block;
  margin-top: 12px;
  padding: 3px;
}
.SeoLinkforminput:hover {
  outline-color: #ef4c23;
}
label ~ select {
  width: 100%;
  font-size: 18px;
  font-weight: 400;
  border-radius: 12px;
  padding: 10px;

  color: gray;
  margin-top: 12px;
  border: 1px solid #ef4c23;
}
._SeoLinkSubmitbutton {
  width: 100%;
  padding: 15px 0;
  text-align: center;
  border: none;
  background: none;
}
._SeoLinkSubmitbutton > button {
  padding: 10px 60px;
  border-radius: 25px;
  background-color: #ef4c23;
  border: none;
  color: white;
  font-size: 18px;
  font-weight: 600;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}
._SeoLinkSubmitbutton > button:hover {
  color: #ef4c23;
  background-color: white;
}
._SeoLinkPricing > button {
  padding: 10px 60px;
  border-radius: 25px;
  background-color: #ef4c23;
  border: none;
  color: white;
  font-size: 18px;
  font-weight: 600;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}
._SeoLinkPricing > button:hover {
  color: #ef4c23;
  /* border: 1px solid #ef4c23; */
  background-color: white;
}

