.exercisebox {
  width: 50%;
  /* background-color: blue; */
  display: flex;
  flex-wrap: wrap;
  padding: 10px;
  height: 600px;
}

.exercisebox > div {
  /* background-color: aliceblue; */
  width: 48%;
  height: 180px;
  border-radius: 10px;
  /* box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px; */
  margin: 6px;
  padding: 12px 10px;
  /* position: relative; */
  /* display: flex; */
}
.exercisebox > div:hover {
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  transform: scale(1.01);
  transition: all .2s linear;
}
.exercisebox > div > p {
  padding: 5x;
}

