._Iphonedevelopmentpage {
  background-color: white;
  margin-top: 45px;
  width: 100%;
  height: 700px;
  display: flex;
}

.iosText {
  width: 50%;
  /* background-color: green; */
  position: relative;
}
.iosText > div {
  width: 90%;
  padding: 10px;
  /* background-color: green; */
  position: absolute;
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  color: #16003e;
}
.iosText > div > button {
  font-weight: 600;
  font-size: 18px;
  padding: 10px 60px;
  border: 2px solid #ef4c23;
  color: #16003e;
  background-color: #ef4c23;
  border-radius: 25px;
}
.iosImage {
  width: 50%;
  /* background-color: red; */
  position: relative;
}
.macimage {
  position: absolute;
  width: 80%;
  height: 80%;
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  /* background-color: pink; */
}
.macimage > img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}
.animeImage {
  position: absolute;
  width: 55%;
  height: 40%;
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  /* background-color: pink; */
}
.animeImage > img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}
