.Ecommerce-book-call-main {
  height: 400px;

  display: flex;
  align-items: center;
  justify-content: center;
}

.Ecommerce-book-call-main > div {
  background-color: #fcdbd3;

  text-align: center;

  padding: 60px 100px;
  border-radius: 20px;
}

.Ecommerce-book-call-main > div > h1 {
  font-size: 32px;
  margin-bottom: 25px;
}

.Ecommerce-book-call-main > div > button {
  font-size: 18px;
  font-weight: 600;
  color: white;
  background-color: #ef4c23;

  border-radius: 100px;
  padding: 10px 60px;

  border: none;
  outline: none;

  cursor: pointer;

  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}

.Ecommerce-book-call-main > div > button:hover {
  background-color: white;
  color: #ef4c23;
  /* box-shadow: none; */
}
