.navbarSection {
  display: flex;
  justify-content: space-around;
  width: 100%;
  height: 80px;
  position: fixed;
  top: 0;
  z-index: 1000000000;
  background: transparent;
  align-items: center;
  background-color: white;
}

.navbarSection.active {
  background: white;
  /* transition: all 0.1s linear; */
  /* position: relative; */
  border-bottom: 0.5px solid rgb(232, 227, 227);
}

.navbarSection > {
  margin-bottom: 0;
  display: flex;
  align-items: center;
}
.navbarSection li {
  color: white;
  font-size: 15px;
  font-weight: 400;
}

.logoSection {
  width: 13%;
  height: 100%;
}
.logoSection img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.dropdownBar {
  width: 73%;
  /* height: 100%; */
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.dropdownBar > div {
  height: fit-content;
  width: 70%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.dropdownBar > div li {
  /* background-color: green; */
  height: 75px;
  width: 30%;
  text-align: center;
  padding: 29px 5px;
  color: black;
  font-weight: 500;
  cursor: pointer;
}

.dropdownBar > div li:hover {
  text-decoration: underline;
  text-decoration-color: #ef4c23;
  text-underline-offset: 10px;
  text-decoration-thickness: 3px;
}

.callToAction {
  background-color: #ef4c23;
  color: white;
  padding: 7px 25px;
  border-radius: 25px;
  font-weight: 600;
  margin: 0 5px;
  border: none;
}
.callToAction:hover {
  color: #ef4c23 !important;
  background-color: white !important;
  border: 1px solid white;
}
.navbarSection.active .dropdownBar > div li:nth-child(8) {
  color: white !important;
  background-color: #ef4c23;
  border: 1px solid #ef4c23;
}
.navbarSection.active .dropdownBar > div li {
  color: #ef4c23;
  /* background-color: #ef4c23; */
  /* border: 1px solid #ef4c23; */
}

.navbarSection.active .dropdownBar > div li:nth-child(8):hover {
  background-color: white !important;
  color: black !important;
}
.navbarSection.active .dropdownBar > div li:nth-child(9) {
  color: white !important;
  background-color: #ef4c23;
  border: 1px solid #ef4c23;
}
.navbarSection.active .dropdownBar > div li:nth-child(9):hover {
  background-color: white !important;
  color: #ef4c23 !important;
}

