/* for media query of navbar */
#checkbox {
  display: none;
}
.bars {
  display: none;
}
#bar {
  display: none;
}

.skillVisible {
  width: 100%;
  box-sizing: border-box;
  background-color: white;
  z-index: 1000000000;
  height: 452px;
  position: fixed;
  top: 78px;
  /* transition: all 0.01s linear; */
}

@media (max-width: 480px) {
  .navbarSection {
    background: #ef4c23;
    justify-content: space-between;
    padding: 0 20px;
    height: 60px;
  }

  .logoSection {
    width: 130px;
  }
  .dropdownBar {
    position: fixed;
    top: 60px;
    left: -100%;
    flex-direction: column;
    width: 100%;
    background-color: green;
  }
  .dropdownBar > div li {
    margin: 5px;
    padding: 2px;
  }

  .dropdownBar > div li:nth-child(1) {
    padding: 5px;
  }
  #checkbox:checked ~ .dropdownBar {
    left: 0;
  }
  #bar {
    display: block;
    color: white;
  }
  .navbarSection.active .bars {
    color: #ef4c23;
  }
  .bars {
    display: block;
    font-size: 24px;
    color: white;
  }

  .callToAction {
    padding: 7px !important;
    margin: 5px !important;
    width: 150px;
    text-align: center;
  }
}

