.szdatingbrand {
  width: 100%;
  height: 440px;
  /* background-color: red; */
  position: relative;
}
.szdatingbrand > h1 {
  text-align: center;
  margin-bottom: 30px;
}
.szdatingbrand > div {
  position: absolute;
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  width: 100%;
  height: 400px;
  /* background-color: green; */
}

.datingbrandhomeServices {
  width: 300px;
  /* background-color: gray; */
  height: 100%;
  padding: 20px;
  margin-top: 20px;
}
.datingbrandhomeServices > div {
  width: 100%;
  border-radius: 10px;
  height: 100%;
  padding: 15px;
  /* background-color: green; */
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}
.datingbrandhomeServices > div > img {
  width: 100%;
  height: 50%;
  object-fit: cover;
}
.datingbrandhomeServices > div > h5 {
  margin-top: 5px;
}
.datingbrandhomeServices > div > h5 {
  margin-top: 5px;
}
