.datingdiv{
    /* background-color: aqua; */
    width: 100%;
    height: 650px;
    margin-top: 6%;
}
.datingflexdiv{
    display: flex;
    width: 90%;
    height: 630px;
    margin: auto;
}
.datingtext{
    width: 50%;
    /* background-color: red; */
    position: relative;
}
.datinglogodiv{
    width: 50%;
    /* background-color: blue; */
}
.datinglogo{
    width: 78%;
    height: 640px;
    margin-left: 15%;
}
.datinglogo>img{
    width: 100%;
    height: 100%;
}
.datingtext>div{
    width: 100%;
    padding: 30px 20px;
    position: absolute;
    transform: translate(-50%,-50%);
    top: 50%;
    left: 50%;
}